import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import FileUploader from 'devextreme-react/file-uploader';
import ProgressBar from 'devextreme-react/progress-bar';
import {nanoid} from 'nanoid';


const OrderImage = ({width, height}) => {
  const classes = useStyles('test')
  const [isDropZoneActive, setIsDropZoneActive] = useState(false);
  const [imageSource, setImageSource] = useState('');
  const [textVisible, seTextVisible] = useState(true);
  const [progressVisible, setProgressVisible] = useState(false);
  const [progressValue, setProgressValue] = useState(0);

  const allowedFileExtensions = ['.jpg', '.jpeg', '.gif', '.png'];

  const onDropZoneEnter = (e) => {
    if(e.dropZoneElement.id === 'dropzone-external') {
      setIsDropZoneActive(true);
    }
  }

  const onDropZoneLeave = (e) => {
    if(e.dropZoneElement.id === 'dropzone-external') {
      setIsDropZoneActive(true);
    }
  }

  const onUploaded = (e) => {
    const file = e.file;
    const fileReader = new FileReader();
    fileReader.onload = () => {
      setIsDropZoneActive(false)
      setImageSource(fileReader.result)
    };
    fileReader.readAsDataURL(file);
    seTextVisible(false)
    setProgressVisible(false)
    setProgressValue(0)
  }

  const onProgress = (e) => {
    setProgressValue(e.bytesLoaded / e.bytesTotal * 100)
  }

  const onUploadStarted = () => {
    setImageSource('')
    setProgressValue(true)
  }


  return (
      <div className={classes.orderImageBlock}>
        <div className="widget-container flex-box make_order">
          <div id="dropzone-external" className={`flex-box ${classes.uploadProgress} ${isDropZoneActive ? 'dx-theme-accent-as-border-color dropzone-active' : 'dx-theme-border-color'}`}>
            {imageSource && <img id="dropzone-image" src={imageSource} alt="" />}
            {textVisible &&
            <div id="dropzone-text" className="flex-box">
              <span>–ó–∞–≥—Ä—É–∑–∏—Ç—å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ</span>
            </div>}
            <ProgressBar
              className={classes.uploadProgress}
              min={0}
              max={100}
              width="30%"
              showStatus={false}
              visible={progressVisible}
              value={progressValue}
            ></ProgressBar>
          </div>
          <FileUploader
            id={nanoid(5)}
            dialogTrigger="#dropzone-external"
            dropZone="#dropzone-external"
            multiple={false}
            allowedFileExtensions={allowedFileExtensions}
            uploadMode="instantly"
            uploadUrl="https://js.devexpress.com/Demos/NetCore/FileUploader/Upload"
            visible={false}
            onDropZoneEnter={onDropZoneEnter}
            onDropZoneLeave={onDropZoneLeave}
            onUploaded={onUploaded}
            onProgress={onProgress}
            onUploadStarted={onUploadStarted}
          ></FileUploader>
        </div>
      </div>
  )
}

const useStyles = makeStyles((theme) => ({
  orderImageBlock: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center',
    width: '200px',
    height: '200px',
    border: '1px solid #000',
    marginRight: '15px',
    '@media (max-width:668px)': {
      width: '100px',
      height: '100px',
    },
    '@media (max-width:375px)': {
      width: '100%',
      height: '200px',
      marginBottom: '20px',
    }
  },
  dropzoneExternal: {
    width: '200px',
    height: '200px',
    backgroundColor: 'rgba(183, 183, 183, 0.1)',
    borderWidth: '2px',
    padding: '10px',
  },
  dropzoneImage: {
    maxWidth: '100%',
    maxHeight: '100%',
  },
  uploadProgress: {
    display: 'flex',
    marginTop: '10px',
  }
}))

export default OrderImage;
